<template>
  <div>
    <input v-model="value" type="text" name="" id="" />
    <button @click="confirm()">判断</button>
    <button @click="reset()">重置</button>
    <div>{{ result }}</div>
    <div>
      基础<input type="text" v-model="base" /> 强化<input
        type="text"
        v-model="strengthen"
      />
      最大<input type="text" v-model="percentage" /> 其他<input
        type="text"
        v-model="others"
      />
      <div>
        <button @click="compute">计算</button>
        <div>{{ attack }}</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      result: false,
      value: '',
      base: '',
      strengthen: '',
      percentage: '',
      others: 2000,
      attack: 0
    }
  },
  methods: {
    compute() {
      let weapon = this.base * (1 + this.strengthen / 100)
      let percent = this.percentage / 100
      this.attack =
        18698 +
        (weapon + 1800 + this.others) * (1 + percent) +
        16140 * percent +
        321
    },
    confirm() {
      let str = this.value
      let stack = []
      let change = true
      for (let i = 0; i < str.length; i++) {
        if (str[i] === '{' || str[i] === '[' || str[i] === '(') {
          stack.push(str[i])
        }
        if (str[i] === ')') {
          if (stack[stack.length - 1] === '(') {
            stack.pop()
          } else {
            change = false
            break
          }
        }
        if (str[i] === ']') {
          if (stack[stack.length - 1] === '[') {
            stack.pop()
          } else {
            change = false
            break
          }
        }
        if (str[i] === '}') {
          if (stack[stack.length - 1] === '{') {
            stack.pop()
          } else {
            change = false
            break
          }
        }
      }
      this.result = stack.length === 0 && change ? true : false
    },
    reset() {
      this.value = ''
    }
    // instance_of(left, right) {
    //   const RP = right.prototype
    //   while (true) {
    //     if (left === null) {
    //       return false
    //     }
    //     if (left === RP) {
    //       return true
    //     }
    //     left = left._proto_
    //   }
    // }
    //
  }
}
</script>

